	module.exports = {"data":{"post":{"id":"E:/gatsby-starter-personal-blog/content/posts/2017-06-21--unreal-engine-coding-standards-part-v/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-e62a2.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 41.36029411764706%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\"\n        alt=\"Unreal Engine Coding Standards\"\n        title=\"\"\n        src=\"/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-78f2b.jpg\"\n        srcset=\"/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-dce19.jpg 200w,\n/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-c1413.jpg 400w,\n/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-78f2b.jpg 800w,\n/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-ab4c4.jpg 1200w,\n/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-bc99b.jpg 1600w,\n/static/photo-33795443263_fcb0014fd2_o-53e6a8500c308508ebc8f8d8d2f28053-e62a2.jpg 2176w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<blockquote>\n<p>Learn C++ from scratch. How to make your first video game in Unreal engine. Gain confidence in programming.\nThis is a fork of the Part I of the first section of the <a href=\"https://github.com/UnrealCourse\">Unreal Course</a> teaching C++ coding standards for the Unreal Game Engine.\nThe Source Code can be found in <a href=\"https://github.com/mpolinowski/consoleApplication\">consoleApplication</a>\nThe following is the commented Course Journal:</p>\n</blockquote>\n<h3>Using using for Type Aliases</h3>\n<ul>\n<li>We’re substituting types to be “Unreal ready”</li>\n<li>The declaration is <strong>using &#x3C;alias> = &#x3C;type>;</strong></li>\n<li>For example <strong>using int32 = int;</strong></li>\n<li>Why Unreal uses <strong>int32</strong> rather than <strong>int</strong></li>\n<li><strong>FText</strong> is for output, <strong>FString</strong> is “mutable”</li>\n<li>Where to use each type of string</li>\n<li>Map <strong>FText</strong> and <strong>FString</strong> to <strong>std::string</strong></li>\n</ul>\n<h3>Using struct for Simple Types</h3>\n<ul>\n<li><strong>struct</strong> is almost identical to <strong>class</strong></li>\n<li>It’s member variables (data) is public by default</li>\n<li>Ideal for simple value types like <strong>BullCowCount</strong></li>\n<li>Outline <strong>BullCowCount SubmitGuess(FString)</strong></li>\n</ul>\n<h3>Using if Statements in C++</h3>\n<ul>\n<li>Why we need conditionals (selection)</li>\n<li>Use <strong>if</strong> when it reads better (e.g. few conditions)</li>\n<li>Use <strong>switch</strong> for multiple, simple conditions</li>\n<li>(for loads of statements consider a table lookup)</li>\n<li>The syntax of an <strong>if</strong> statement</li>\n<li>Using <strong>if</strong> to write count bulls and cows.</li>\n</ul>\n<h3>Debugging 101</h3>\n<ul>\n<li>A very brief intro to Visual Studio’s debugger</li>\n<li>Set a break-point by clicking in margin</li>\n<li>Watch values by highlighting in debug mode</li>\n<li>Use “Continue” to cycle back to breakpoint.</li>\n</ul>\n<h3>A Place for Everything</h3>\n<ul>\n<li>Centralising the hidden word length</li>\n<li>Making this a property of the game class</li>\n<li>Writing a getter to access this value</li>\n<li>Updating our intro to vary with word length.</li>\n</ul>\n<h3>Introducing enumerations</h3>\n<ul>\n<li>An <strong>enum</strong>erated type consists of named values</li>\n<li>Use instead of coded meaning</li>\n<li>Makes the code more readable and meaningful</li>\n<li>Only defined values can be used - more robust</li>\n<li>A benefit of C++ 11’s strongly typed enums</li>\n<li>Creating an <strong>enum class</strong> for error checking.</li>\n</ul>\n<h3>Writing Error Checking Code</h3>\n<ul>\n<li>Use <strong>else if</strong> for the first time</li>\n<li>Outline or <strong>CheckGuessValidity()</strong> method</li>\n<li>Write working code for checking guess length</li>\n<li>Use the debugger to test the return values.</li>\n</ul>\n<h3>Using switch Statements</h3>\n<ul>\n<li>Use our error values to communicate with user</li>\n<li>All our user interaction is via <strong>GameManager.cpp</strong></li>\n<li>We’ll use <strong>FText</strong> in this file, as it’s UI text</li>\n<li>We can “switch” what we say based on the error</li>\n<li>The syntax of a <strong>switch</strong> statement</li>\n<li>Remember your <strong>break</strong> keywords!</li>\n</ul>\n<h3>Warm Fuzzy Feelings</h3>\n<ul>\n<li><em>Don’t</em> get comfortable with compiler warnings</li>\n<li>Refactor <strong>GetValidGuess()</strong> to remove warning</li>\n<li>Rename <strong>SubmitGuess()</strong> to <strong>SubmitValidGuess()</strong></li>\n<li>Improve readability of <strong>SubmitValidGuess()</strong></li>\n<li>Get a warm fuzzy feeling!</li>\n</ul>\n<h3>Handling Game Win Condition</h3>\n<ul>\n<li>Change our <strong>PlayGame()</strong> loop to a <strong>while</strong></li>\n<li>Implement our <strong>IsGameWon()</strong> function</li>\n</ul>\n<h3>Win or Lose “Screen”</h3>\n<p>Write a method to print a game summary to the screen once the game is over.</p>\n<h3>Introducing Big O Notation</h3>\n<ul>\n<li>Algorithm: the recipe for solving a problem</li>\n<li>or: 45th US Vice President’s dance style</li>\n<li>Introducing the complexity of algorithms</li>\n<li>A quick introduction to “Big O” notation</li>\n<li>Comparing three ways of checking for isograms.</li>\n</ul>\n<h3>TMap and map Data Structures</h3>\n<ul>\n<li>The importance of knowing your data types</li>\n<li>Introducing the <strong>std::map</strong> data type</li>\n<li><strong>#define TMap std::map</strong> to keep it ‘Unreal’</li>\n<li>How we’ll be using the map</li>\n<li><strong>TMap&#x3C;char, bool> LetterSeen;</strong> to declare</li>\n<li>Using <strong>LetterSeen[Letter]</strong> to access</li>\n<li>Wiring-up and pseudocoding <strong>IsIsogram()</strong></li>\n</ul>\n<h3>Range-based for Loop</h3>\n<ul>\n<li>Introducing containers and iterators</li>\n<li>Using a range-based for loop in Unreal*</li>\n<li>Gently introducing the auto keyword</li>\n<li>Finishing our IsIsogram()</li>\n</ul>\n<p><strong>Useful Links</strong></p>\n<ul>\n<li>* <a href=\"https://www.unrealengine.com/blog/ranged-based-for-loops\">Unreal Engine - Ranged Based For Loops</a></li>\n</ul>\n<h3>Design a Helper Function</h3>\n<ul>\n<li>Gain confidence with a multi-stage challenge</li>\n<li>A word on implicit dependencies</li>\n</ul>\n<h3>Playtesting Your Game</h3>\n<ul>\n<li>Having someone else play test your game is vital</li>\n<li>Silently take notes, or record screen if possible</li>\n<li>Immediately go away and fix obvious bugs</li>\n<li>For improvements consider 2nd or 3rd opinion</li>\n<li>Repeat until the bug / issue rate plateaus.</li>\n</ul>\n<h3>Difficulty &#x26; Play Tuning</h3>\n<ul>\n<li>About the flow channel*</li>\n<li><strong>map</strong> word length to max tries</li>\n<li>Play test to determine correct difficulty.</li>\n</ul>\n<p><strong>Useful Links</strong></p>\n<ul>\n<li>* Read more in <a href=\"https://www.amazon.com/dp/B00AWKX1FO/\">Sylvester, T. Designing Games - O’Reilly</a></li>\n</ul>\n<h3>Polishing &#x26; Packaging</h3>\n<ul>\n<li>First impressions count (think reviews)</li>\n<li>Don’t ship a half-baked product, even if digital</li>\n<li>Check through your code (polish)</li>\n<li>Ship to your customers (package).</li>\n</ul>\n<h3>Section 2 Wrap-Up</h3>\n<ul>\n<li>HUGE congratulations on your progress</li>\n<li>Over 5 hours of pure C++ learning</li>\n<li>Over 30 challenges you’ve completed</li>\n<li>The journey has only just begun</li>\n<li>Share your source code for others to play</li>\n<li>Here are some suggested improvements</li>\n<li>Next we take the game logic into Unreal :-)</li>\n</ul>","fields":{"slug":"/unreal-engine-coding-standards-part-v/","prefix":"2017-06-21"},"frontmatter":{"title":"Unreal Engine Coding Standards (V)","subTitle":"The Unreal Engine Developer Course - Learn C++ & Make Games. Learn C++ from scratch. How to make your first video game in Unreal engine. Gain confidence in programming.","cover":{"childImageSharp":{"resize":{"src":"/static/photo-34605459545_86a3d9a34f_o-cover-1e34d083024d04749e7ea7dec32dec59-ada8c.jpg"}}}}},"author":{"id":"E:/gatsby-starter-personal-blog/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mike Polinowski</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/contact/\">contact me</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. :hearts:</p>"},"footnote":{"id":"E:/gatsby-starter-personal-blog/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>This is a personal Developer Blog</li>\n<li>Ξ</li>\n<li>built by <a href=\"https://github.com/mpolinowski\">Mike Polinowski</a></li>\n<li>Ξ</li>\n<li>with <a href=\"https://www.gatsbyjs.org/\">Gatsby</a> &#x26; <a href=\"https://reactjs.org\">React</a></li>\n<li>Ξ</li>\n<li>source code on <a href=\"https://github.com/mpolinowski/mpolinowski.github.io\">GitHub</a></li>\n<li>Ξ</li>\n<li>deliverd by <a href=\"https://pages.github.com\">Github Pages</a></li>\n<li>Ξ</li>\n<li>photos from <a href=\"https://www.flickr.com/people/149680084@N06/\">Flickr</a></li>\n</ul>"}},"pathContext":{"slug":"/unreal-engine-coding-standards-part-v/"}}

